import React, { Component } from 'react';
import { withRouter } from 'react-router';
import moment from 'moment';
import request from '../../request';
import CrossWithBorder from '../../assets/svg/cross-with-border.svg';
import TickWithBorder from '../../assets/svg/tick-with-border.svg';
import BorderSvg from '../../assets/svg/border.svg';
import Logo from '../../assets/svg/logo1.svg';


class ShowReportScreen extends Component {
    state = {
        report: {},
        loaded: false
    }
    titles = {
        _screenShadow: "Ekranda glgelenme",
        _touchScreen: "Dokunmatik Ekran",
        _frontCameraSound: "n Kamera ve Mikrofonu",
        _backCameraSound: "Arka Kamera ve Mikrofonu",
        _flashLight: "Flash I",
        _cellularNetwork: "ebeke",
        _bluetooth: "Bluetooth",
        _wifiConnection: "Wi-Fi",
        _incomingNoise: "Gelen Ses",
        _outgoingNoise: "Giden Ses",
        _vibration: "Titreim",
        _charging: "arj Soketi",
        _batteryCapacity: "Pil Kapasitesi",
        _directionSensor: "Yn Sensr",
        _balanceSensor: "Denge Sensr",
        _distanceSensor: "Mesafe Sensr",
        _compassSensor: "Pusula",
        _pressureSensor: "Basn Sensr",
        _gpsSensor: "GPS",
        _nfcSensor: "NFC",
        _memory: "Hafza / Saklama Alan",
        _ram: "RAM",
        _processor: "lemci",
        _remoteCharging: "Kablosuz arj",
        _screws: "Eksiksiz Vidalama",
        _sideKeysVolume: "Yan Ses Tular",
        _sideKeysTurnOn: 'Yan "Ama Kapama" Tuu',
        _sideKeysMute: 'Yan "Sessize Al" Tuu',
        _homeKey: "Ana Ekran (Home Tuu)",
        _fingerPrint: "Parmak zi zellii",
        _faceId: "FaceID / Yz Tanma",
        _iCloud: "iCloud",

    }
    constructor(props) {
        super(props)
    }
    componentDidMount() {
        request.get(`/reports/${this.props.match.params.id}`).then((res) => {
            this.setState({ report: res.data, loaded: true })
        }).catch(e => {
            console.error(e)
            //alert("Rapor Bulunamad")
        })
    }
    //HEADER STARTS
    renderHeader() {
        return (
            <div className="container header">
                <div className="row">
                    <div className="col-lg-6 col-12">
                        <embed src={Logo} />
                        <div className="dateTime">
                            <div className="date">
                                <span>Tarih:{"  "}</span>
                                {
                                    moment(this.state.report?.createdAt).format("LL")
                                }
                            </div>
                            <div className="hour">
                                <span>Saat:{"  "}</span>
                                {
                                    moment(this.state.report?.createdAt).format("LT")
                                }
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-6 col-12">
                        <div className="report-info">
                            <span>Mteri Ad:</span>
                            {" Orkun ***** "}
                        </div>
                        <div className="report-no">
                            <span>Rapor No:</span>
                            {" EC-002131234"}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    //HEADER ENDS

    //HEADER BOTTOM LAYER STARTS
    renderHeaderBottomLayer() {
        return (
            <div class="header-bottom-layer">
                <div className="header-bottom-layer-flex-1 flex-vertical">
                    <div>
                        <div className="report-title">
                            levsellik Durumu
                        </div>
                        <div className="report-title-description">
                            Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                        </div>
                    </div>
                </div>
                <div className="header-bottom-layer-flex-1 flex-end">
                    <div className="report-analysis">
                        <span>25</span> Uygun / <span>9</span> Arzal
                    </div>
                </div>
            </div>
        )
    }
    //HEADER BOTTOM LAYER ENDS
    //FUNCTIONALITY COMPONENT STARTS
    renderPositiveBoxes() {
        return (
            <div className="boxes d-flex justify-content-center">
                <div className="d-flex box">Uygun <img src={TickWithBorder} /></div>
                <div className="d-flex box">Arzal <img src={BorderSvg} /></div>
            </div>
        )
    }
    renderNegativeBoxes() {
        return (
            <div className="boxes d-flex justify-content-center">
                <div className="d-flex box">Uygun <img src={BorderSvg} /></div>
                <div className="d-flex box">Arzal <img src={CrossWithBorder} /></div>
            </div>
        )
    }
    renderOneFunctionalityComponent(config = {
        type: false,
        note: "Note yok",
        title: ""
    }) {
        return (
            <div className="col-lg-6 col-12 func-all">
                <div className={`func ${config?.type === true ? "positive" : "negative"}`}>
                    <div className="functionality-report-element-top">
                        <div className="flexed-top">
                            <div className="title">
                                <div className="titleText">
                                    {config?.title}
                                </div>
                            </div>
                            {config?.type === true
                                ? this.renderPositiveBoxes()
                                : this.renderNegativeBoxes()}
                        </div>
                        <div className={`d-flex flex-row functionality-report-element-note`} style={{
                            display: !config?.note ? "none !important" : "block !important"
                        }}>
                            <div className="note-title">
                                <div className="titleText">
                                    Not:
                                </div>
                            </div>
                            <div className="note">
                                <div className="noteText">
                                    {config?.note || "Not bulunmamaktadr"}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div >
        )
    }
    renderFunctionality() {
        return (
            <div className="container">
                <div className="row no-gutters">
                    {Object.keys(this.state.report.functionality).filter((key, index) => {
                        const item = this.state.report.functionality?.[key];
                        if (item?.checkbox === null) {
                            return false;
                        }
                        return true;
                    }).map((key, index) => {
                        const item = this.state.report.functionality?.[key];
                        return this.renderOneFunctionalityComponent({
                            type: item?.checkbox,
                            note: item?.note,
                            title: this.titles[key]
                        })
                    })
                    }

                </div>
            </div>
        )
    }
    //FUNCTIONALITY COMPONENT ENDS
    /* <div style={{ width: "100vw", minHeight: "100vh", backgroundColor: "white" }}>
                    {Object.keys(this.state.report).map((key, i) => {
                        return <div>{`${key}: ${JSON.stringify(this.state.report[key])}`}</div>
                    })}
                </div> */
    render() {
        if (!this.state.loaded) return <div></div>
        return (
            <div class="container-fluid report-container">
                <div className="container web-report">
                    {this.renderHeader()}
                    {this.renderHeaderBottomLayer()}
                    {this.renderFunctionality()}
                </div>
            </div>
        )
    }
}

export default withRouter(ShowReportScreen);